/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@font-face {
  font-family: GilroyExtraBold;
  src: url("./assets/fonts/Gilroy-ExtraBold_0.ttf"); }

@font-face {
  font-family: GilroyLight;
  src: url("./assets/fonts/Gilroy-Light.otf"); }

@font-face {
  font-family: GilroyMedium;
  src: url("./assets/fonts/Gilroy-Medium_0.ttf"); }

@font-face {
  font-family: GilroySemiBold;
  src: url("./assets/fonts/Gilroy-SemiBold_0.ttf"); }

@font-face {
  font-family: MinionProRegular;
  src: url("./assets/fonts/MinionPro-Regular.otf"); }

@font-face {
  font-family: OxygenBold;
  src: url("./assets/fonts/Oxygen-Bold.otf"); }

@font-face {
  font-family: oxygenlight;
  src: url("./assets/fonts/oxygen.light.ttf"); }

@font-face {
  font-family: oxygenregular;
  src: url("./assets/fonts/oxygen.regular.ttf"); }

@font-face {
  font-family: segoeui;
  src: url("./assets/fonts/segoeui.ttf"); }

@font-face {
  font-family: segoeuib;
  src: url("./assets/fonts/segoeuib.ttf"); }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: oxygenregular; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

textarea:focus,
input:focus,
select:focus,
button:focus {
  border-color: none !important;
  outline: 0;
  -webkit-box-shadow: none !important;
  box-shadow: none !important; }

body {
  line-height: 1;
  overflow-x: hidden; }

ol, ul, li {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

h1 {
  font-family: GilroyExtraBold;
  font-size: 3.5em;
  letter-spacing: .1em; }

h3 {
  font-family: GilroyExtraBold;
  font-size: 1.2em; }

h4 {
  font-family: GilroyExtraBold; }

p {
  font-family: oxygenlight;
  line-height: 2em; }

.container {
  width: 80vw;
  margin: 0 auto; }

.banner01 {
  width: 100vw;
  height: 100vh;
  background: url("../../assets/img/BG_Banner_home.png") no-repeat; }
  .banner01 h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #00e3f5;
    position: relative;
    font-size: 4.5;
    top: 40%;
    transform: translate(0, -50%);
    max-width: 80vw;
    margin: 0 auto; }

.btnDowload01 {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  position: relative;
  top: 50%;
  border: solid 1px #00e3f5;
  max-width: 18em;
  padding: 1em 1.2em 1em 0;
  font-family: GilroyExtraBold;
  color: #00e3f5;
  background: url("../../assets/img/icon/icon-download.png") no-repeat 93% 50%;
  transition: all .2s; }
  .btnDowload01:hover {
    background-position: 93% 55%; }

.social_temp {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  position: relative;
  top: 60%;
  width: 40%; }
  .social_temp a {
    transition: all .2s; }
    .social_temp a:hover {
      transform: scale(1.08); }
    .social_temp a img {
      width: 40%; }

.feito_com_amor {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  position: relative;
  top: 70%;
  width: 80vw;
  margin: 0 auto;
  color: #00e3f5; }
  .feito_com_amor i {
    color: red; }

.feito_com_amor p:nth-child(1) {
  justify-self: start; }

.feito_com_amor p:nth-child(2) {
  justify-self: center; }

.feito_com_amor p:nth-child(3) {
  justify-self: end; }

.proposito {
  background: #e8e9ec;
  padding: 5em 0; }

.grid_proposito {
  display: grid;
  justify-items: center;
  grid-template-columns: 1fr 1fr;
  width: 80vw;
  margin: 0 auto; }
  .grid_proposito h1 {
    color: #00e3f5;
    font-size: 3.5em;
    font-family: GilroyExtraBold; }
  .grid_proposito p {
    max-width: 70%;
    margin: 3em 0 0 0;
    padding: 0 0 1em 1em;
    border-left: 7px solid #00e3f5;
    font-size: 1.1em; }
  .grid_proposito img {
    width: 70%;
    margin: 0 auto; }
  .grid_proposito form {
    display: grid;
    grid-template-columns: 1fr;
    max-width: 70%; }
    .grid_proposito form input, .grid_proposito form textarea {
      margin: 0 auto;
      background: none;
      border-top: none;
      border-right: none;
      border-bottom: solid 1px #00e3f5;
      border-left: none;
      margin: 1em 0;
      padding: 1em 0 1em 1em; }
    .grid_proposito form ::placeholder {
      color: #666; }
    .grid_proposito form textarea {
      padding: 1em 0 5em 1em; }
    .grid_proposito form button {
      width: 20%;
      border-radius: none;
      border: none;
      background: #00e3f5;
      font-family: GilroyExtraBold;
      color: #fff;
      padding: .5em 0;
      justify-self: end; }

@media screen and (max-width: 900px) {
  .banner01 h1 {
    text-align: center; }
  .feito_com_amor {
    grid-template-columns: 1fr;
    top: 65%; }
  .feito_com_amor p:nth-child(1) {
    justify-self: center; }
  .feito_com_amor p:nth-child(2) {
    justify-self: center; } }

.ensinar_fazer {
