/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@font-face {
  font-family: GilroyExtraBold;
  src: url("./assets/fonts/Gilroy-ExtraBold_0.ttf"); }

@font-face {
  font-family: GilroyLight;
  src: url("./assets/fonts/Gilroy-Light.otf"); }

@font-face {
  font-family: GilroyMedium;
  src: url("./assets/fonts/Gilroy-Medium_0.ttf"); }

@font-face {
  font-family: GilroySemiBold;
  src: url("./assets/fonts/Gilroy-SemiBold_0.ttf"); }

@font-face {
  font-family: MinionProRegular;
  src: url("./assets/fonts/MinionPro-Regular.otf"); }

@font-face {
  font-family: OxygenBold;
  src: url("./assets/fonts/Oxygen-Bold.otf"); }

@font-face {
  font-family: oxygenlight;
  src: url("./assets/fonts/oxygen.light.ttf"); }

@font-face {
  font-family: oxygenregular;
  src: url("./assets/fonts/oxygen.regular.ttf"); }

@font-face {
  font-family: segoeui;
  src: url("./assets/fonts/segoeui.ttf"); }

@font-face {
  font-family: segoeuib;
  src: url("./assets/fonts/segoeuib.ttf"); }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: oxygenregular; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

textarea:focus,
input:focus,
select:focus,
button:focus {
  border-color: none !important;
  outline: 0;
  -webkit-box-shadow: none !important;
  box-shadow: none !important; }

body {
  line-height: 1;
  overflow-x: hidden; }

ol, ul, li {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

h1 {
  font-family: GilroyExtraBold;
  font-size: 3.5em;
  letter-spacing: .1em; }

h3 {
  font-family: GilroyExtraBold;
  font-size: 1.2em; }

h4 {
  font-family: GilroyExtraBold; }

p {
  font-family: oxygenlight;
  line-height: 2em; }

.container {
  width: 80vw;
  margin: 0 auto; }

.footer {
  display: none;
  background: #000;
  padding: 3em 0;
  color: #00e3f5; }
  .footer a {
    color: #00e3f5; }

.destaque {
  font-family: GilroyExtraBold !important;
  font-size: 1em !important; }

.social_footer {
  justify-self: start; }
  .social_footer .item_social_footer {
    margin: 0 1em 0 0; }

.grid_footer {
  display: grid;
  grid-template-columns: .3fr 1fr; }

.footer_left {
  justify-self: start; }
  .footer_left .links_footer {
    margin: 3em 0; }
    .footer_left .links_footer li {
      width: 150px;
      margin: 1.5em 0;
      padding-bottom: .1em; }
      .footer_left .links_footer li a {
        border-bottom: 1px solid #00e3f5;
        font-family: GilroyExtraBold;
        letter-spacing: .1em; }
  .footer_left span {
    font-size: .7em;
    white-space: nowrap; }

.footer_right {
  justify-self: end;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 2em;
  width: 70%;
  text-align: right; }
  .footer_right li {
    margin: 1em 0;
    font-family: GilroyLight;
    font-size: .9em;
    letter-spacing: .1em;
    white-space: nowrap; }
  .footer_right .eta_footer {
    justify-self: end; }
