/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@font-face {
  font-family: GilroyExtraBold;
  src: url("./assets/fonts/Gilroy-ExtraBold_0.ttf"); }

@font-face {
  font-family: GilroyLight;
  src: url("./assets/fonts/Gilroy-Light.otf"); }

@font-face {
  font-family: GilroyMedium;
  src: url("./assets/fonts/Gilroy-Medium_0.ttf"); }

@font-face {
  font-family: GilroySemiBold;
  src: url("./assets/fonts/Gilroy-SemiBold_0.ttf"); }

@font-face {
  font-family: MinionProRegular;
  src: url("./assets/fonts/MinionPro-Regular.otf"); }

@font-face {
  font-family: OxygenBold;
  src: url("./assets/fonts/Oxygen-Bold.otf"); }

@font-face {
  font-family: oxygenlight;
  src: url("./assets/fonts/oxygen.light.ttf"); }

@font-face {
  font-family: oxygenregular;
  src: url("./assets/fonts/oxygen.regular.ttf"); }

@font-face {
  font-family: segoeui;
  src: url("./assets/fonts/segoeui.ttf"); }

@font-face {
  font-family: segoeuib;
  src: url("./assets/fonts/segoeuib.ttf"); }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  font-family: oxygenregular; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

textarea:focus,
input:focus,
select:focus,
button:focus {
  border-color: none !important;
  outline: 0;
  -webkit-box-shadow: none !important;
  box-shadow: none !important; }

body {
  line-height: 1;
  overflow-x: hidden; }

ol, ul, li {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

h1 {
  font-family: GilroyExtraBold;
  font-size: 3.5em;
  letter-spacing: .1em; }

h3 {
  font-family: GilroyExtraBold;
  font-size: 1.2em; }

h4 {
  font-family: GilroyExtraBold; }

p {
  font-family: oxygenlight;
  line-height: 2em; }

.container {
  width: 80vw;
  margin: 0 auto; }

.header {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: start;
  justify-content: center;
  position: absolute;
  width: 90vw;
  top: 1.5em;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, 0); }
  .header .social_header {
    justify-self: start; }
    .header .social_header .item_social_header {
      margin: 0 1em; }
  .header .logo_header {
    justify-self: center; }
  .header .fazer_ensinar {
    justify-self: end;
    position: relative;
    z-index: 999; }
  .header .fazer, .header .ensinar {
    position: relative;
    z-index: 999;
    color: #00e3f5;
    font-family: GilroyExtraBold;
    margin: 0 1.5em;
    font-size: 1.2em;
    float: left;
    cursor: pointer; }
  .header .fazer:hover {
    color: #000; }
    .header .fazer:hover .menu_fazer {
      display: block; }

.fazer_aparecer {
  font-family: GilroyExtraBold;
  z-index: 999; }

.menu_fazer {
  display: block;
  position: relative;
  margin: -1.3em 0 0 -5em;
  background: #00e3f5;
  padding: 3em 1em 1em 1em;
  color: #000;
  z-index: 20;
  width: 200px; }
  .menu_fazer li {
    margin: .5em 0;
    border-bottom: 1px solid #000;
    text-align: right;
    padding: 0 0 .5em 0;
    font-family: GilroyLight;
    font-size: .9em; }
